import React, { Component } from "react";
import { connect } from "react-redux";
import { reset } from "redux-form";
import Swal from "sweetalert2";
import { getClosePengambilanBarcode } from "../../actions/closeBarcodeAction";
import {
  getBruto,
  getNetto,
  getPkg,
  getQty,
  getRp,
} from "../../actions/LogicAction";
import { getTotalNW } from "../../actions/returnPenjualanAction";
import { getListTransaksiPenjualan } from "../../actions/transaksiPenjualanAction";
import { gagal, warning } from "../../Alert";
import { AxiosMasterGet } from "../../Axios";
import { getToday } from "../../getDate";
import GlobalTabel from "../../GlobalComponent/GlobalTabel";

class FormBarcodePenjualanCustomer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "baki",
          text: "Nama Baki",
        },
        {
          dataField: "instansi",
          text: "Supplier",
        },
        {
          dataField: "jenis_trx",
          text: "Kode Model",
        },
        {
          dataField: "total_bruto",
          text: "Berat(Gr)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "total_netto",
          text: "Netto(Gr)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "total_ongkos",
          text: "Ongkos(Rp)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "total_gross",
          text: "Gross(Gr)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-warning mr-2"
                  onClick={() => this.pilihBarcode(row)}
                >
                  Pilih
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getClosePengambilanBarcode());
  }

  async pilihBarcode(data) {
    let sebelum = JSON.parse(localStorage.getItem("transaksiPenjualan")) || [];
    let hasil = data.detail_barang;
    hasil.forEach((list, index) => {
      AxiosMasterGet(`jenis/get/by-kode-jenis/${hasil[index].kode_jenis}`)
        .then((res) => {
          var simpan = {
            netto_disc:
              (parseFloat(hasil[index].bruto) * res.data.kadar_jual) / 100,
            gross:
              parseFloat(hasil[index].bruto) + parseFloat(hasil[index].pkg), //berat + bungkus
            netto: (parseFloat(hasil[index].bruto) * res.data.kadar_jual) / 100, //murni
            qty: 1,
            k: 0,
            ongkos_rp: hasil[index].ongkos_rp, //ongkos
            kadar_cetak: (hasil[index].kadar_cetak || 0).toString(),
            disc_persen: 0,
            kadar_jual: res.data.kadar_jual, //harga%
            kadar_level2: res.data.level2,
            kadar_level3: res.data.level3,
            kadar_modal: res.data.kadar_beli, //harga%
            kode_jenis: hasil[index].kode_jenis, //kodebarang
            kode_barcode: "-",
            kode_barcode_set: "-",
            p: 0,
            disc: 0,
            pkg: hasil[index].pkg,
            bruto: hasil[index].bruto, //berat
          };
          var simpan_level2 = {
            netto_disc:
              (parseFloat(hasil[index].bruto) * res.data.level2) / 100,
            gross:
              parseFloat(hasil[index].bruto) + parseFloat(hasil[index].pkg), //berat + bungkus
            netto: (parseFloat(hasil[index].bruto) * res.data.level2) / 100, //murni
            qty: 1,
            k: 0,
            ongkos_rp: hasil[index].ongkos_rp, //ongkos
            kadar_cetak: (hasil[index].kadar_cetak || 0).toString(),
            disc_persen: 0,
            kadar_jual: res.data.kadar_jual, //harga%
            kadar_level2: res.data.level2,
            kadar_level3: res.data.level3,
            kadar_modal: res.data.kadar_beli, //harga%
            kode_jenis: hasil[index].kode_jenis, //kodebarang
            kode_barcode: "-",
            kode_barcode_set: "-",
            p: 0,
            disc: 0,
            pkg: hasil[index].pkg,
            bruto: hasil[index].bruto, //berat
          };
          var simpan_level3 = {
            netto_disc:
              (parseFloat(hasil[index].bruto) * res.data.level3) / 100,
            gross:
              parseFloat(hasil[index].bruto) + parseFloat(hasil[index].pkg), //berat + bungkus
            netto: (parseFloat(hasil[index].bruto) * res.data.level3) / 100, //murni
            qty: 1,
            k: 0,
            ongkos_rp: hasil[index].ongkos_rp, //ongkos
            kadar_cetak: (hasil[index].kadar_cetak || 0).toString(),
            disc_persen: 0,
            kadar_jual: res.data.kadar_jual, //harga%
            kadar_level2: res.data.level2,
            kadar_level3: res.data.level3,
            kadar_modal: hasil[index].kadar_beli, //harga%
            kode_jenis: hasil[index].kode_jenis, //kodebarang
            kode_barcode: "-",
            kode_barcode_set: "-",
            p: 0,
            disc: 0,
            pkg: hasil[index].pkg,
            bruto: hasil[index].bruto, //berat
          };
          var array =
            JSON.parse(localStorage.getItem("transaksiPenjualan")) || [];
          var array_level2 =
            JSON.parse(localStorage.getItem("transaksiPenjualan_level2")) || [];
          var array_level3 =
            JSON.parse(localStorage.getItem("transaksiPenjualan_level3")) || [];
          let posisi = sebelum.findIndex(
            (data) =>
              list.kode_jenis === data.kode_jenis || data.netto === list.netto
          );
          console.log(list.netto);
          console.log(posisi);
          // return false;
          if (posisi < 0) {
            array.push(simpan);
            array_level2.push(simpan_level2);
            array_level3.push(simpan_level3);
            var GrandTotalNetto = array
              .map((list) => parseFloat(list.netto_disc))
              .reduce((a, b) => a + b, 0);
            localStorage.setItem("GrandTotalNetto", GrandTotalNetto);
            localStorage.setItem("transaksiPenjualan", JSON.stringify(array));
            localStorage.setItem(
              "transaksiPenjualan_level2",
              JSON.stringify(array_level2)
            );
            localStorage.setItem(
              "transaksiPenjualan_level3",
              JSON.stringify(array_level3)
            );
            Swal.fire({
              position: "top-end",
              icon: "success",
              text: "Data Berhasil Ditambahkan!",
              showConfirmButton: false,
              timer: 1500,
            });
            this.props.dispatch(getListTransaksiPenjualan());
            this.props.dispatch(getTotalNW());
            this.props.dispatch(reset("FormTransaksiPenjualan"));

            this.props.dispatch(getNetto("transaksiPenjualan"));
            this.setState({
              discGlobalGr:
                (parseFloat(0) / 100) *
                parseFloat(localStorage.getItem("GrandTotalNetto")),
              discGlobalPr: 0,
              GrandTotalNetto: (
                parseFloat(localStorage.getItem("GrandTotalNetto")) - 0
              ).toFixed(3),
            });
          } else {
            warning("Data Sudah Ada Di Tabel");
          }
        })
        .catch((err) =>
          gagal("Kode Kelompok Belum Disesuaikan, Mohon Sesuaikan Dahulu")
        );
    });
  }
  getSumData() {
    this.props.dispatch(getNetto("ReturnPenjualan"));
    this.props.dispatch(getBruto("ReturnPenjualan"));
    this.props.dispatch(getRp("ReturnPenjualan"));
    this.props.dispatch(getPkg("ReturnPenjualan"));
    this.props.dispatch(getQty("ReturnPenjualan"));
  }

  render() {
    return (
      <div className="col-lg-12 col-md-12">
        <GlobalTabel
          keyField="kodeJenis"
          data={
            this.props.listCloseBarcode.filter(
              (list) =>
                list.jenis_trx === "PENJUALAN" &&
                list.tanggal === getToday() &&
                list.instansi === this.props.kodeCustomer
            ) || []
          }
          columns={this.state.columns}
          textEmpty="Data Barcode Kosong, Silahkan Tambah barcode dahulu untuk memakai fitur ini"
          // link="/ambilbarcode"
          // location="Rekam Barang Barcode"
        />
      </div>
    );
  }
}

export default connect((state) => {
  return {
    listCloseBarcode: state.provinsi.listCloseBarcode,
  };
})(FormBarcodePenjualanCustomer);
