import React, { Component } from "react";
import { connect } from "react-redux";
import { reset } from "redux-form";
import Swal from "sweetalert2";
import { getClosePengambilanTimbangan } from "../../actions/closeTimbangan";
import { getListBarang } from "../../actions/kodeBarangAction";
import {
  finishSend,
  getBruto,
  getNetto,
  getPkg,
  getQty,
  getRp,
  progressSend,
} from "../../actions/LogicAction";
import { getTotalNW } from "../../actions/returnPenjualanAction";
import { getListTransaksiPenjualan } from "../../actions/transaksiPenjualanAction";
import { gagal } from "../../Alert";
import { AxiosMasterGet } from "../../Axios";
import { getToday } from "../../getDate";
import GlobalTabel from "../../GlobalComponent/GlobalTabel";
import { setFocus } from "../../helper";

class FormTimbanganPenjualanCustomer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "baki",
          text: "Nama Baki",
        },
        {
          dataField: "kode_customer",
          text: "Supplier",
        },
        {
          dataField: "jenis_trx",
          text: "Kode Model",
        },
        {
          dataField: "total_bruto",
          text: "Berat(Gr)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "total_netto",
          text: "Netto(Gr)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "total_ongkos",
          text: "Ongkos(Rp)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "total_gross",
          text: "Gross(Gr)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-warning mr-2"
                  onClick={() => this.pilihData(row, row.kode_customer)}
                >
                  Pilih
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getClosePengambilanTimbangan());
    this.props.dispatch(getListBarang());
  }

  pilihData(data, customer) {
    data.detail_barang.map((detail) => {
      let json = detail;
      json["netto_disc"] = detail.netto;
      json["disc"] = 0;
      json["disc_persen"] = 0;
      json["harga_jual"] = 0;
      // json["kode_kategori"] = "GLE17K"
      this.addBarang(json, customer);
      return true;
    });
  }

  async addBarang(data, customer) {
    this.props.dispatch(progressSend());
    let param = data.kode_jenis;
    let hasil = this.props.kodeBarang.filter(
      (data) => data.kode_jenis === param
    );
    let kode_tukar_1 = 0;
    let kode_tukar_2 = 0;
    let kode_tukar_3 = 0;
    let customer_1 = customer;
    let customer_2 =
      this.state.level_customer === "LEVEL2"
        ? this.state.kodeCustomer
        : localStorage.getItem("customer_2");
    let customer_3 =
      this.state.level_customer === "LEVEL3" ? this.state.kodeCustomer : "-";
    await AxiosMasterGet(
      `kelompok-harga/get/by-jenis-and-customer/${customer_1}&${data.kode_jenis}`
    )
      .then((res) => (kode_tukar_1 = res.data.kode_tukar_normal || 0))
      .catch(() => (kode_tukar_1 = 0));
    await AxiosMasterGet(
      `kelompok-harga/get/by-jenis-and-customer/${customer_2}&${data.kode_jenis}`
    )
      .then((res) => (kode_tukar_2 = res.data.kode_tukar_normal || 0))
      .catch(() => (kode_tukar_2 = 0));
    await AxiosMasterGet(
      `kelompok-harga/get/by-jenis-and-customer/${customer_3}&${data.kode_jenis}`
    )
      .then((res) => (kode_tukar_3 = res.data.kode_tukar_normal || 0))
      .catch(() => (kode_tukar_3 = 0));
    AxiosMasterGet(`jenis/get/by-kode-jenis/${data.kode_jenis}`)
      .then((res) => {
        var simpan_nota_1 =
          JSON.parse(localStorage.getItem("nota_penjualan_level1")) || [];
        var simpan_nota_2 =
          JSON.parse(localStorage.getItem("nota_penjualan_level2")) || [];
        var simpan_nota_3 =
          JSON.parse(localStorage.getItem("nota_penjualan_level3")) || [];
        var nota_gambar = JSON.parse(localStorage.getItem("nota_gambar")) || [];
        var posisi = simpan_nota_1.findIndex(
          (find) => find.kode_kategori === data.kode_kategori
        );
        var posisi_2 = simpan_nota_2.findIndex(
          (find) => find.kode_kategori === data.kode_kategori
        );
        var posisi_3 = simpan_nota_3.findIndex(
          (find) => find.kode_kategori === data.kode_kategori
        );
        nota_gambar.push({ lokasi_gambar: res.data?.image });
        localStorage.setItem("nota_gambar", JSON.stringify(nota_gambar));
        if (posisi !== -1) {
          var nota_1 = {
            netto_disc: (
              parseFloat(data.netto_disc || 0) +
              parseFloat(simpan_nota_1[posisi].netto_disc)
            ).toFixed(3),
            netto: (
              parseFloat(data.netto || 0) +
              parseFloat(simpan_nota_1[posisi].netto)
            ).toFixed(3),
            ongkos_rp: (
              parseFloat(data.ongkos_rp === undefined ? 0 : data.ongkos_rp) +
              simpan_nota_1[posisi].ongkos_rp
            ).toLocaleString("id-ID"), //ongkos
            disc: (
              parseFloat(data.disc || 0) +
              parseFloat(simpan_nota_1[posisi].disc)
            ).toFixed(3),
            bruto: (
              parseFloat(data.bruto || 0) +
              parseFloat(simpan_nota_1[posisi].bruto)
            ).toFixed(3), //berat
            kode_kategori: data.kode_kategori,
            kode_tukar: kode_tukar_1,
            keterangan: data.keterangan,
            jumlah_barang_set: 0,
            total_berat_barang_set: 0,
            kode_barcode_set: "-",
            harga_jual: data.harga_jual === "undefined" ? 0 : data.harga_jual,
          };
          var nota_2 = {
            netto_disc: (
              (parseFloat(data.bruto || 0) * parseFloat(kode_tukar_2)) / 100 -
              (parseFloat(data.disc_persen) * parseFloat(data.bruto || 0)) /
                100 +
              parseFloat(simpan_nota_2[posisi].netto_disc)
            ).toFixed(3),
            netto: (
              ((parseFloat(data.bruto || 0) * parseFloat(kode_tukar_2)) / 100 ||
                0) + parseFloat(simpan_nota_2[posisi].netto)
            ).toFixed(3), //murni
            ongkos_rp:
              parseFloat(data.ongkos_rp === undefined ? 0 : data.ongkos_rp) +
              parseFloat(simpan_nota_2[posisi].ongkos_rp), //ongkos
            disc: (
              (parseFloat(data.disc_persen) / 100) *
                parseFloat(data.bruto || 0) +
              parseFloat(simpan_nota_2[posisi].disc)
            ).toFixed(3),
            bruto:
              parseFloat(data.bruto || 0) +
              parseFloat(simpan_nota_2[posisi].bruto), //berat
            kode_kategori: data.kode_kategori,
            kode_tukar: kode_tukar_2,
            keterangan: data.keterangan,
            jumlah_barang_set: 0,
            total_berat_barang_set: 0,
            kode_barcode_set: "-",
            harga_jual: data.harga_jual === "undefined" ? 0 : data.harga_jual,
          };
          var nota_3 = {
            netto_disc: (
              (parseFloat(data.bruto || 0) * parseFloat(kode_tukar_3)) / 100 -
              (parseFloat(data.disc_persen) * parseFloat(data.bruto || 0)) /
                100 +
              parseFloat(simpan_nota_3[posisi].netto_disc)
            ).toFixed(3),
            netto: (
              ((parseFloat(data.bruto || 0) * parseFloat(kode_tukar_3)) / 100 ||
                0) + parseFloat(simpan_nota_3[posisi].netto)
            ).toFixed(3), //murni
            ongkos_rp:
              parseFloat(data.ongkos_rp === undefined ? 0 : data.ongkos_rp) +
              parseFloat(simpan_nota_3[posisi].ongkos_rp), //ongkos
            disc: (
              (parseFloat(data.disc_persen) / 100) *
                parseFloat(data.bruto || 0) +
              parseFloat(simpan_nota_3[posisi].disc)
            ).toFixed(3),
            bruto:
              parseFloat(data.bruto || 0) +
              parseFloat(simpan_nota_3[posisi].bruto), //berat
            kode_kategori: data.kode_kategori,
            kode_tukar: kode_tukar_3,
            keterangan: data.keterangan,
            jumlah_barang_set: 0,
            total_berat_barang_set: 0,
            kode_barcode_set: "-",
            harga_jual: data.harga_jual === "undefined" ? 0 : data.harga_jual,
          };
          simpan_nota_1.push(nota_1);
          simpan_nota_2.push(nota_2);
          simpan_nota_3.push(nota_3);
          simpan_nota_1.splice(posisi, 1);
          simpan_nota_2.splice(posisi_2, 1);
          simpan_nota_3.splice(posisi_3, 1);
          localStorage.setItem(
            "nota_penjualan_level1",
            JSON.stringify(simpan_nota_1)
          );
          localStorage.setItem(
            "nota_penjualan_level2",
            JSON.stringify(simpan_nota_2)
          );
          localStorage.setItem(
            "nota_penjualan_level3",
            JSON.stringify(simpan_nota_3)
          );
        } else {
          var nota_1_else = {
            netto_disc: parseFloat(data.netto_disc || 0).toFixed(3),
            netto: parseFloat(data.netto || 0).toFixed(3), //murni
            ongkos_rp: parseFloat(
              data.ongkos_rp === undefined ? 0 : data.ongkos_rp
            ), //ongkos
            disc: data.disc || 0,
            bruto: parseFloat(data.bruto || 0).toFixed(3), //berat
            kode_kategori: data.kode_kategori,
            kode_tukar: kode_tukar_1,
            keterangan: data.keterangan,
            disc_persen: data.disc_persen || 0,
            jumlah_barang_set: 0,
            total_berat_barang_set: 0,
            kode_barcode_set: "-",
            harga_jual:
              data.harga_jual === "undefined"
                ? 0
                : parseFloat(data.harga_jual) * parseFloat(data.qty),
          };
          var nota_2_else = {
            netto_disc: (
              (parseFloat(data.bruto || 0) * parseFloat(kode_tukar_2)) / 100 -
              (parseFloat(data.disc_persen) * parseFloat(data.bruto || 0)) / 100
            ).toFixed(3),
            netto: (
              (parseFloat(data.bruto || 0) * parseFloat(kode_tukar_2)) / 100 ||
              0
            ).toFixed(3), //murni
            ongkos_rp: parseFloat(
              data.ongkos_rp === undefined ? 0 : data.ongkos_rp
            ), //ongkos
            disc: (
              (parseFloat(data.disc_persen) / 100) *
              parseFloat(data.bruto || 0)
            ).toFixed(3),
            bruto: parseFloat(data.bruto || 0), //berat
            kode_kategori: data.kode_kategori,
            kode_tukar: kode_tukar_2,
            keterangan: data.keterangan,
            jumlah_barang_set: 0,
            total_berat_barang_set: 0,
            kode_barcode_set: "-",
            harga_jual:
              data.harga_jual === "undefined"
                ? 0
                : parseFloat(data.harga_jual) * parseFloat(data.qty),
          };
          var nota_3_else = {
            netto_disc: (
              (parseFloat(data.bruto || 0) * parseFloat(kode_tukar_3)) / 100 -
              (parseFloat(data.disc_persen) * parseFloat(data.bruto || 0)) / 100
            ).toFixed(3),
            netto: (
              (parseFloat(data.bruto || 0) * parseFloat(kode_tukar_3)) / 100 ||
              0
            ).toFixed(3), //murni
            ongkos_rp: parseFloat(
              data.ongkos_rp === undefined ? 0 : data.ongkos_rp
            ), //ongkos
            disc: (
              (parseFloat(data.disc_persen) / 100) *
              parseFloat(data.bruto || 0)
            ).toFixed(3),
            bruto: parseFloat(data.bruto || 0), //berat
            kode_kategori: data.kode_kategori,
            kode_tukar: kode_tukar_3,
            keterangan: data.keterangan,
            jumlah_barang_set: 0,
            total_berat_barang_set: 0,
            kode_barcode_set: "-",
            harga_jual:
              data.harga_jual === "undefined"
                ? 0
                : parseFloat(data.harga_jual) * parseFloat(data.qty),
          };
          simpan_nota_1.push(nota_1_else);
          simpan_nota_2.push(nota_2_else);
          simpan_nota_3.push(nota_3_else);
          localStorage.setItem(
            "nota_penjualan_level1",
            JSON.stringify(simpan_nota_1)
          );
          localStorage.setItem(
            "nota_penjualan_level2",
            JSON.stringify(simpan_nota_2)
          );
          localStorage.setItem(
            "nota_penjualan_level3",
            JSON.stringify(simpan_nota_3)
          );
        }
        var simpan = {
          netto_disc: parseFloat(data.netto_disc || 0).toFixed(3) || 0,
          gross: parseFloat(data.gross || 0).toFixed(3) || 0, //berat + bungkus
          netto: parseFloat(data.netto || 0).toFixed(3) || 0, //murni
          qty: data.qty || 1,
          k: parseFloat(data.k || 0),
          ongkos_rp: parseFloat(
            data.ongkos_rp === undefined ? 0 : data.ongkos_rp
          ), //ongkos
          kadar_cetak: (hasil[0].kadar_cetak || 0).toString(),
          disc_persen: data.disc_persen || 0,
          kadar_jual: kode_tukar_1 || 0, //harga%
          kadar_level2: res.data.level2 || 0,
          kadar_level3: res.data.level3 || 0,
          kadar_modal: hasil[0].kadar_modal || 0, //harga%
          kode_jenis: hasil[0].kode_jenis || 0, //kodebarang
          kode_barcode: "-",
          kode_barcode_set: "-",
          p: parseFloat(data.p || 0),
          disc: data.disc || 0,
          pkg: parseFloat(data.pkg || 0),
          bruto: parseFloat(data.bruto || 0).toFixed(3), //berat
          harga_jual: data.harga_jual === "undefined" ? 0 : data.harga_jual,
        };

        var array =
          JSON.parse(localStorage.getItem("transaksiPenjualan")) || [];
        array.push(simpan);
        var GrandTotalNetto = array
          .map((list) => parseFloat(list.netto))
          .reduce((a, b) => a + b, 0);
        localStorage.setItem("GrandTotalNetto", GrandTotalNetto);
        localStorage.setItem("transaksiPenjualan", JSON.stringify(array));
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Berhasil Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        });
        this.props.dispatch(finishSend());
        this.props.dispatch(getListTransaksiPenjualan());
        this.props.dispatch(getTotalNW());
        this.props.dispatch(reset("FormTransaksiPenjualan"));
        setFocus("discGlobal_penjualan");
        this.props.dispatch(getNetto("transaksiPenjualan"));
        this.setState({
          discGlobalGr:
            (parseFloat(0) / 100) *
            parseFloat(localStorage.getItem("GrandTotalNetto")),
          discGlobalPr: 0,
          GrandTotalNetto: (
            parseFloat(localStorage.getItem("GrandTotalNetto")) - 0
          ).toFixed(3),
        });
      })
      .catch((err) =>
        gagal("Kode Kelompok Belum Disesuaikan, Mohon Sesuaikan Dahulu")
      );
  }
  getSumData() {
    this.props.dispatch(getNetto("ReturnPenjualan"));
    this.props.dispatch(getBruto("ReturnPenjualan"));
    this.props.dispatch(getRp("ReturnPenjualan"));
    this.props.dispatch(getPkg("ReturnPenjualan"));
    this.props.dispatch(getQty("ReturnPenjualan"));
  }

  render() {
    return (
      <div className="col-lg-12 col-md-12">
        <GlobalTabel
          keyField="kodeJenis"
          data={
            this.props.listCloseBarcode.filter(
              (list) =>
                list.jenis_trx === "PENJUALAN" &&
                list.tanggal === getToday() &&
                list.kode_customer === this.props.kodeCustomer
            ) || []
          }
          columns={this.state.columns}
          textEmpty="Data Timbangan Kosong"
        />
      </div>
    );
  }
}

export default connect((state) => {
  return {
    listCloseBarcode: state.provinsi.listCloseTimbangan,
    kodeBarang: state.provinsi.kodeBarang,
  };
})(FormTimbanganPenjualanCustomer);
