import React, { Component, lazy, Suspense } from "react";
import { connect } from "react-redux";
import { reduxForm, reset } from "redux-form";
import SelectSearch from "react-select-search";
import { getUUID } from "../actions/fakturAction";
import Swal from "sweetalert2";
import { getListTransaksiPenjualan } from "../actions/transaksiPenjualanAction";
import {
  finishSend,
  getBruto,
  getGross,
  getK,
  getNetto,
  getP,
  getPkg,
  getQty,
  getRp,
  progressSend,
} from "../actions/LogicAction";
import { warning } from "../Alert";
import { getTotalNW } from "../actions/returnPenjualanAction";
import Skeleton from "react-loading-skeleton";
import { showModal } from "../actions/ModalAction";
import HeaderCard from "../HeaderCard";
import { getLokasi } from "../actions/lokasiAction";
import { getsalesmanList } from "../actions/salesmanAction";
import { getcustomerList } from "../actions/customerAction";
import { AxiosMasterPost } from "../Axios";
import GlobalTabel from "../GlobalComponent/GlobalTabel";
import FormBarcodePenjualanCustomer from "./Component/FormBarcodePenjualanCustomer";
import FormTimbanganPenjualanCustomer from "./Component/FormTimbanganPenjualanCustomer";
import { getToday } from "../GlobalComponent/function";
import { getListBarangPo } from "../actions/preOrder";
import ReactTooltip from "react-tooltip";
import PrintNotaSuratJalan from "../Stocking/Component/PrintNotaSuratJalan";
import { getUserID } from "../helper";

const ModalMasterData = lazy(() =>
  import("../DataMaster/Component/ModalMasterData")
);
const FormTransaksiPO = lazy(() => import("./Component/FormTransaksiPO"));
const maptostate = (state) => {
  return {
    noFaktur: state.provinsi.noFaktur,
    barangPO: state.po.barangPO_temp,
    listCustomer: state.provinsi.customer,
    listLokasi: state.provinsi.lokasi,
    listSales: state.provinsi.salesman,
    totalNW: state.provinsi.totalNW,
    username: state.provinsi.username,
    onSend: state.provinsi.onSend,
    kodeBarang: state.provinsi.kodeBarang,
  };
};
class PreOrder extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listLokasi: [],
      listSales: [],
      listCustomer: [],
      kodeCustomer: "",
      columns: [
        {
          dataField: "kode_jenis",
          text: "Kode Model",
        },
        {
          dataField: "request_warna_batu",
          text: "Request Warna Batu",
        },
        {
          dataField: "request_lain",
          text: "Request Lain",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  data-tip
                  data-for="hapus"
                  className="btn btn-danger mr-2"
                  onClick={() => this.deleteRow(rowIndex)}
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  <i className="fa fa-trash mr-1"></i>
                </button>
                <br />
              </div>
            );
          },
        },
      ],
      noBon: "",
      namaCustomer: "",
      lokasi: "",
      sales: "",
      namaToko: "",
      toko: "",
      tanggal: "",
      data: "",
    };
  }

  deleteRow(rowIndex) {
    Swal.fire({
      title: "Anda Yakin ?",
      text: "Anda Akan Menghapus Data Ini ?",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      if (result.isConfirmed) {
        const array = JSON.parse(localStorage.getItem("barangPO_temp")) || [];
        array.splice(rowIndex, 1);
        localStorage.setItem("barangPO_temp", JSON.stringify(array));
        Swal.fire({
          text: "Berhasil",
          timer: 2000,
          showConfirmButton: false,
          position: "top-right",
          icon: "success",
        })
          .then(() => this.props.dispatch(getListBarangPo()))
          .then(() =>
            this.setState({
              deleted: true,
            })
          );
      }
    });
  }
  componentDidMount() {
    this.props.dispatch(getLokasi());
    this.props.dispatch(getsalesmanList());
    this.props.dispatch(getcustomerList());
    this.props.dispatch(getUUID());
    localStorage.removeItem("barangPO_temp");
    localStorage.removeItem("GrandTotalNetto");
    this.props.dispatch(getListBarangPo());
    this.props.dispatch(getTotalNW());
    this.props.dispatch(getBruto("transaksiPenjualan"));
    this.props.dispatch(getNetto("transaksiPenjualan"));
    this.props.dispatch(getPkg("transaksiPenjualan"));
    this.props.dispatch(getRp("transaksiPenjualan"));
    this.props.dispatch(getGross("transaksiPenjualan"));
    this.props.dispatch(getP("transaksiPenjualan"));
    this.props.dispatch(getK("transaksiPenjualan"));
    this.props.dispatch(getQty("transaksiPenjualan"));
    this.setState({
      GrandTotalNetto: (
        parseFloat(localStorage.getItem("GrandTotalNetto") || 0) - 0
      ).toFixed(3),
    });
  }

  handleSubmit(data) {
    let param = data.kodeBarang;
    let hasil = this.props.kodeBarang.filter(
      (data) => data.kode_jenis === param
    );
    var simpan = {
      kode_jenis: hasil[0].kode_jenis || 0, //kodebarang
      request_warna_batu: data.request_warna_batu,
      request_lain: data.request_lain || "-",
    };
    var array = JSON.parse(localStorage.getItem("barangPO_temp")) || [];
    array.push(simpan);
    localStorage.setItem("barangPO_temp", JSON.stringify(array));
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
    this.props.dispatch(getListBarangPo());
    this.props.dispatch(getTotalNW());
    this.props.dispatch(reset("FormTransaksiPO"));
  }

  transaksiPenjualanSubmit() {
    if (localStorage.getItem("barangPO_temp") === null) {
      warning("Barang masih kosong, Silahkan tambah barang");
      return false;
    } else if (this.state.lokasi === "") {
      warning("Mohon pilih lokasi");
      return false;
    } else if (this.state.sales === "") {
      warning("Mohon pilih Sales");
      return false;
    } else if (this.state.namaToko === "") {
      warning("Mohon pilih toko");
      return false;
    }
    const no_po = "PO-" + Date.now();
    var simpan = {
      no_preorder: no_po,
      kode_lokasi: this.state.lokasi.toUpperCase(),
      kode_sales: this.state.sales.toUpperCase(),
      kode_customer: this.state.kodeCustomer.toUpperCase(),
      tanggal_selesai: this.state.tanggal || getToday(2),
      tanggal: getToday(),
      detail_barang: JSON.parse(localStorage.getItem("barangPO_temp")),
    };
    this.props.dispatch(progressSend());
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = JSON.parse(localStorage.getItem("barangPO_temp"));
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        ++i,
        ticket.kode_jenis,
        ticket.request_warna_batu,
        ticket.request_lain,
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    var columTable = [["No", "Kode", "Request Warna Batu", "Request Lain"]];
    // AKHIR INISIALISASI AUTOTABLE
    AxiosMasterPost("pre-order/simpan", simpan)
      .then(() =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Berhasil Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => {
            PrintNotaSuratJalan(
              "No Nota",
              no_po,
              "Tanggal",
              getToday(),
              "LEVEL",
              "ADMIN INTERN",
              "",
              "",
              "ADMIN",
              getToday(),
              "",
              columTable,
              "Bukti PO",
              tableRows,
              [],
              "Bukti PO"
            );
            PrintNotaSuratJalan(
              "No Nota",
              no_po,
              "Tanggal",
              getToday(),
              "Customer",
              this.state.namaCustomer,
              "",
              "",
              getUserID(),
              getToday(),
              "",
              columTable,
              "Bukti PO",
              tableRows,
              [],
              "Bukti PO"
            );
          })
          .then(() => this.props.dispatch(finishSend()))
          .then(() => localStorage.removeItem("transaksiPenjualan"))
          .then(() => localStorage.removeItem("GrandTotalNetto"))
          .then(() => localStorage.removeItem("noFaktur"))
          .then(() => this.props.dispatch(getUUID()))
          .then(() => this.props.dispatch(getTotalNW()))
          .then(() => this.props.dispatch(getListTransaksiPenjualan()))
          .then(() =>
            this.setState({
              noBon: "",
              namaCustomer: "",
              lokasi: "",
              sales: "",
              namaToko: "",
              tanggal: "",
              toko: "",
              GrandTotalNetto: 0,
            })
          )
          .then(() => document.getElementById("myForm").reset())
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: `${err.response.data}`,
          showConfirmButton: false,
          timer: 1500,
        }).then(() => this.props.dispatch(finishSend()))
      );
    console.log(JSON.stringify(simpan));
  }
  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };
  onSubmit(data, input) {
    this.setState({
      [input]: data,
    });
  }
  onSubmitToko(data, input) {
    var split = data.split("|");
    localStorage.setItem("customer_1", split[0]);
    this.setState({
      kodeCustomer: split[0],
      namaCustomer: split[1],
      namaToko: split[2],
      levelCustomer: split[3],
      toko: data,
    });
  }

  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12">
          <div className="card">
            <HeaderCard title="Transaksi Pre Order" />
            <div className="card-body">
              <div className="row">
                <div className="col-lg-12">
                  <form
                    id="myForm"
                    onKeyDown={(event) => {
                      return event.key !== "Enter";
                    }}
                  >
                    <div className="row">
                      <div className="col-lg-4 col-md-4">
                        <div className="form-group">
                          <label htmlFor="">Tanggal Selesai</label>
                          <input
                            name="tanggal"
                            type="date"
                            label="Tanggal"
                            className="form-control"
                            onChange={this.inputChange("tanggal")}
                            defaultValue={getToday(2)}
                          />
                          <span>
                            Tanggal ini sudah ditambah 2 hari dari tanggal
                            sekarang
                          </span>
                        </div>
                      </div>
                      <div className="col-lg-4 col-md-4">
                        {/* <div className="form-group">
                          <label htmlFor="">No Bon</label>
                          <input
                            name="noBon"
                            type="text"
                            label="No Bon"
                            className="form-control"
                            onChange={this.inputChange("noBon")}
                            value={this.state.noBon}
                          />
                        </div> */}
                      </div>
                      <div className="col-lg-4 col-md-4"></div>
                      <div className="col-lg-3 col-md-3">
                        <div className="form-group">
                          <label htmlFor="">Lokasi</label>
                          <SelectSearch
                            options={[
                              {
                                value: "SALES",
                                name: "SALES",
                              },
                              {
                                value: "PUSAT",
                                name: "PUSAT",
                              },
                            ]}
                            search
                            placeholder="Silahkan Pilih"
                            onChange={(data) => this.onSubmit(data, "lokasi")}
                            value={this.state.lokasi}
                          />
                        </div>
                      </div>
                      <div className="col-lg-3 col-md-3">
                        <div className="form-group">
                          <label htmlFor="">Sales</label>
                          <SelectSearch
                            options={this.props.listSales.map((obj) => {
                              var payload = {
                                value: obj.kode_sales,
                                name: obj.nama_sales,
                              };
                              return payload;
                            })}
                            search
                            placeholder="Silahkan Pilih"
                            onChange={(data) => this.onSubmit(data, "sales")}
                            value={this.state.sales}
                          />
                        </div>
                      </div>
                      <div className="col-lg-3 col-md-3">
                        <div className="form-group">
                          <label htmlFor="">Nama Toko</label>
                          <SelectSearch
                            options={this.props.listCustomer.map((obj) => {
                              var payload = {
                                value:
                                  obj.kode_customer +
                                  "|" +
                                  obj.nama_customer +
                                  "|" +
                                  obj.nama_toko +
                                  "|" +
                                  obj.level_customer,
                                name: obj.nama_toko,
                              };
                              return payload;
                            })}
                            search
                            placeholder="Silahkan Pilih"
                            onChange={(data) =>
                              this.onSubmitToko(data, "namaToko")
                            }
                            onBlur={(data) => this.onSubmit(data, "toko")}
                            value={this.state.toko}
                          />
                        </div>
                      </div>
                      <div className="col-lg-3 col-md-3">
                        <div className="form-group">
                          <label htmlFor="">Customer</label>
                          <input
                            name="customer"
                            type="text"
                            className="form-control"
                            label="Customer"
                            onChange={this.inputChange("namaCustomer")}
                            defaultValue={this.state.namaCustomer}
                          />
                        </div>
                      </div>
                    </div>
                  </form>
                </div>
                <div className="col-lg-12 col-md-12 mt-3">
                  <div className="row">
                    <div className="col-lg-4"></div>
                    <div className="col-lg-4"></div>
                    <div className="col-lg-4">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          disabled={this.state.toko === ""}
                          onClick={() => this.props.dispatch(showModal())}
                        >
                          Tambah Barang
                          <i class="fa fa-plus ml-3" aria-hidden="true"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-lg-12 mt-5">
                  <GlobalTabel
                    keyField="id"
                    data={this.props.barangPO}
                    columns={this.state.columns}
                  />
                  <div className="text-right">
                    <button
                      className="btn btn-primary"
                      disabled={this.props.onSend}
                      onClick={() => this.transaksiPenjualanSubmit()}
                    >
                      {this.props.onSend ? (
                        <span
                          className="spinner-border spinner-border-sm mr-3"
                          aria-hidden="true"
                        ></span>
                      ) : null}
                      Simpan
                      <i class="fa fa-paper-plane ml-3" aria-hidden="true"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="barcodeModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-xl">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Data Barcode
                </h4>
                <button
                  type="button"
                  id="dismissModal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <FormBarcodePenjualanCustomer
                  kodeCustomer={this.state.kodeCustomer}
                />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        <div
          id="timbanganModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-xl">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Data Timbangan
                </h4>
                <button
                  type="button"
                  id="dismissModal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <FormTimbanganPenjualanCustomer
                  kodeCustomer={this.state.kodeCustomer}
                />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        <ModalMasterData
          content={
            <Suspense
              fallback={<Skeleton width={"100%"} height={50} count={4} />}
            >
              <FormTransaksiPO onSubmit={(data) => this.handleSubmit(data)} />
            </Suspense>
          }
          title="Tambah Data"
        />
      </div>
    );
  }
}

PreOrder = reduxForm({
  form: "PreOrder",
  enableReinitialize: true,
})(PreOrder);
export default connect(maptostate, null)(PreOrder);
